.App {
  /* display: flex; */
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background-color: black;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none; 
  /* justify-content: center; */
}
.App::-webkit-scrollbar {
  display: none;
}
.banner {
  width: 95vw;
  height: 100vh;
  /* Add the blur effect */
  filter: blur(0px);
  -webkit-filter: blur(0px);
  background: linear-gradient(
      rgba(0, 0, 0, 1) 0%,
      rgba(0, 0, 0, 0.1) 50%,
      rgba(0, 0, 0, 1) 100%
    ),
    /* url(https://images.unsplash.com/photo-1510519138101-570d1dca3d66?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3431&q=80); */
  url('./img/bg1.avif');
  background-repeat: no-repeat;
  background-position: 0;
  background-size: cover;
  transition: all 0.5s;
}
.banner2 {
  width: 95vw;
  height: 200vh;
  /* Add the blur effect */
  filter: blur(5px);
  -webkit-filter: blur(5px);
  background: linear-gradient(
      rgba(0, 0, 0, 1) 10%,
      rgba(0, 0, 0, 0.3) 50%,
      rgba(0, 0, 0, 1) 100%
    ),
    /* url(https://images.unsplash.com/photo-1586202690666-e1f32e218afe?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2100&q=80); */
    url('./img/bg2.avif');
  background-repeat: no-repeat;
  background-position: 0;
  background-size: cover;
  transition: all 0.5s;
}
.banner3 {
  width: 95vw;
  height: 180vh;
  /* Add the blur effect */
  filter: blur(5px);
  -webkit-filter: blur(0px);
  background: linear-gradient(
      rgba(0, 0, 0, 1) 10%,
      rgba(0, 0, 0, 0.3) 50%,
      rgba(0, 0, 0, 1) 100%
    ),
    /* url(https://images.unsplash.com/photo-1487017159836-4e23ece2e4cf?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2102&q=80); */
    url('./img/bg2.avif');
  background-repeat: no-repeat;
  background-position: 0;
  background-size: cover;
  transition: all 0.5s;
}
/* Container holding the image and the text */
.container {
  position: relative;
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* Bottom left text */
.bottom-left {
  z-index: 10;
  animation: 1s ease-in-out 0s 1 slideFromLeft;
  position: fixed;
  bottom: 42%;
  left: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.first {
  animation: 1.25s ease-in-out 0s 1 leftText;
}
.third {
  animation: 1.25s ease-in-out 0s 1 rightText;
}

/* Bottom right text */
.bottom-right {
  z-index: 10;
  animation: 1s ease-in-out 0s 1 slideFromRight;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  bottom: -30%;
  right: 30px;
}

.border {
  backdrop-filter: blur(15px);
  z-index: 2;
  width: 25vw;
  height: 70vh;
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: transparent;
}
.content {
  z-index: 3;
  position: absolute;
}

.skills-holder {
  top: 10%;
  width: 65vw;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.portfolio-holder {
  top: 30%;
  width: 65vw;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.skills-header {
  align-self: center;
  font-size: 45px;
  letter-spacing: 3px;
  border-bottom: 2px solid white;
  transform: all 1s;
  margin-bottom: 50px;
}
.contact-header {
  top: 45%;
  align-self: center;
  font-size: 45px;
  letter-spacing: 3px;
  border-bottom: 2px solid white;
  transform: all 1s;
}
.about-header {
  top: -20%;
  align-self: center;
  font-size: 45px;
  letter-spacing: 3px;
  border-bottom: 2px solid white;
  transform: all 1s;
  margin-bottom: 100px;
}
.dev-header {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.dev-holder {
  width: 100%;
  /* background-color: blue; */
  height: 80px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.icon {
  flex-wrap: wrap;
  flex-direction: column-reverse;
  justify-content: center;
  padding: 10px;
  width: 60px;
  height: 60px;
  transition: all 0.25s;
}
.icon:hover {
  transform: scale(1.1);
}
.body-text {
  font-size: 30px;
  letter-spacing: 2px;
}
.secondary-text {
  opacity: 0.5;
  margin-left: 20px;
  font-size: 13px;
  letter-spacing: 1px;
}

/* Centered text */
.centered {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.line {
  margin-top: 25px;
  margin-bottom: 25px;
  width: 2px;
  height: 37px;
  background-color: rgb(221, 221, 221);
}

.text {

  font-size: 45px;
  writing-mode: vertical-rl;
  text-orientation: upright;
  letter-spacing: 40px;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
  transition: all 0.25s;
}
.second {
  line-height: 0px;
  margin-left: 60px;
  margin-right: 60px;
}
.first,
.third {
  line-height: 120px;
  /* -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(255, 255, 255); */
  /* background-color: white; */
  color: white;
}
.nav-text {
  color: rgb(221, 221, 221);
  font-size: 20px;
  line-height: 100px;
  writing-mode: vertical-rl;
  text-orientation: upright;
  transition: all 0.3s;
}
.nav-text:hover {
  font-weight: bold;
  cursor: pointer;
}
.nav-text:active {
  font-weight: bold;
  cursor: pointer;
}
.contact {
  top: 50%;
  width: 60%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: transparent;
  padding: 80px;
  display: flex;
  backdrop-filter: blur(15px);
  margin-bottom: 30px;
}
.form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  /* overflow: hidden; */
  /* background-color: red; */
}
.textarea-holder{
  width: 100%;
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.email {
  width: 100%;
  height: 50px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.54);
  padding: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  letter-spacing: 1px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
}

.textarea {
  resize:vertical;
  width: 100%;
  height: 200px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.54);
  padding: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  letter-spacing: 1px;
  color: white;
}
.submitButton{
  width: 100%;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  background-color: rgb(0, 0, 0);
  padding: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  letter-spacing: 1px;
  color: white;
  cursor: pointer;
  transition: all 0.25s ease-in-out;

  align-self: flex-end;
}
.submitButton:hover{
  background-color: rgba(255, 255, 255, 0.1);
}
.contact-text{
  font-size: 20px;
  letter-spacing: 1px;
  color: white;
  margin-bottom: 10px;
  margin-left: 10px;
}
.linkedin{
  position: absolute;
  top: 0%;
  display: flex;
  width: 80%;
  /* background-color: red; */
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;

  margin-bottom: 50px;
}
.about-holder{
  top: -13%;
  width: 50%;
  /* background-color: blue; */
  height: 80px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.insta-card{
  border-radius: 5px;
  overflow: hidden;
  /* background-color: red; */
}
.about-text{
  font-size: 20px;
  letter-spacing: 1px;
  color: white;
  margin-bottom: 10px;
  margin-left: 10px;
}
.keyboard-holder{
  cursor: pointer;
  top: 80%;
  z-index: 999;
  position: fixed;
  overflow: hidden;
  opacity: 0.2;
  transition: all 0.25s ease-in-out;

  display: flex;
  width: 100%;
  justify-content: center;
}
.mobile-nav{
  display: none;
}
.nav-text-mobile {
  display: none;
}
.keyboard-on{
  opacity: 1;
}
.keyboard-holder:hover{
  opacity: 1;
}
@keyframes slideFromLeft {
  0% {
    transform: translateX(-100%);
  }
  70% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes slideFromTop {
  0% {
    transform: translateY(-100%);
  }
  70% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes slideFromRight {
  0% {
    transform: translateX(100%);
  }
  70% {
    transform: translateX(-10%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes slideFromBottom {
  0% {
    transform: translateY(100%);
  }
  70% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes leftText {
  0% {
    transform: translateX(100%) translateY(-10%);
  }
  50% {
    transform: translateX(100%) translateY(5%);
  }
  100% {
    transform: translateX(0%) translateY(0%);
  }
}

@keyframes rightText {
  0% {
    transform: translateX(-100%) translateY(10%);
  }
  50% {
    transform: translateX(-100%) translateY(-5%);
  }
  100% {
    transform: translateX(0%) translateY(0%);
  }
}

@media only screen and (min-width:601px) and (max-width:914px)  {
  .skills-holder {
    top: 10%;
  }
  .portfolio-holder {
    top: 30%;
  }
  .about-header{
    top: -20%;
  }
  .about-holder{
    top: -15%;
  }
  .linkedin {
    top: 0%;
  }
  .about-text{
    font-size: 15px;
  }
  .banner3{
    height: 220vh;
  }
  .contact{
    width: 60%;
    top: 62%;
  }
  .contact-header{
    top: 55%;
  }
  .keyboard-holder{
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .skills-header {
    font-size: 30px;
  }
  .mobile-nav-bottom{
    top: 92%;
    z-index: 999;
    position: fixed;
    overflow: hidden;
    opacity: 1;
    transition: all 0.25s ease-in-out;
    animation: 1s ease-in-out 0s 1 slideFromBottom;
  
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: black;
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding-bottom: 200px;
  }
  .mobile-nav-top{
    top: 0%;
    z-index: 999;
    position: fixed;
    overflow: hidden;
    opacity: 1;
    transition: all 0.25s ease-in-out;
    animation: 1s ease-in-out 0s 1 slideFromTop;
  
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: black;
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding-top: 10px;
  }
  .nav-text-mobile {
    display: block;

    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */

    cursor: pointer;
    margin: 10px;
    letter-spacing: 5px;
    color: rgb(255, 255, 255);
    font-size: 25px;
    transition: all 0.3s;
  }

  .bottom-left {
    display: none;
  }
  .bottom-right {
    display: none;
  }
  .skills-holder {
    top: 5%;
    width: 70%;
  }
  .portfolio-holder {
    top: 30%;
    width: 100%;
  }
  .about-header{
    top: -10%;
    font-size: 30px;
  }
  .about-holder{
    top: -5%;
  }
  .about-text{
    font-size: 14px;
  }
  .icon:hover {
    margin-bottom: 0px;
  }
  .banner3{
    height: 300vh;
  }
  .linkedin {
    top: 12%;
  }
  .contact{
    width: 90%;
    top: 66%;
    padding: 30px;
  }
  .contact-header{
    top: 60%;
  }
  .email {
    font-size: 15px;
  }
  .contact-text{
    font-size: 18px;
  }
  .textarea{
    font-size: 15px;
  }
  .keyboard-holder{
    display: none;
  }
}
